/* .storybook > div > div > div > div> div {
  height: 100%;
} */

.storybook, .storybook::before, .storybook::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-right: 1px solid rgba(128, 128, 128, 0.267);
  border-bottom: 1px solid rgba(128, 128, 128, 0.267);
  border-top: 1px solid rgba(128, 128, 128, 0.267);
}

.storybook-font-weight {
  font-weight: lighter;
}

.subheader {
  color: rgba(0, 0, 0, 0.85);
}

.storybook::before {
  left: 3px;
  top: 2px;
  z-index: -1;
  background-color: rgb(247, 247, 247);
  
}

/* Third sheet of paper */
.storybook::after {
  left: 6px;
  top: 4px;
  z-index: -2;
  background-color: rgb(245, 245, 245);
}

.storybook-container {
  position: relative;
  background-color: none;
}

.toc-button {
